import { mapState } from 'vuex'
export default {
  filters: {
    statusFilter (val) {
      switch (val) {
        case 1:
          return '已过期'
        case 2:
          return '已领取'
        case 3:
          return '已领完'
        case 4:
          return '领取红包'
      }
    }
  },
  name: 'Envelope',
  props: {
    item: {
      type: Object
    }
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapState([
      'envelope', 'user'
    ]),
    currentEnvelope () {
      const envelopeId = this.item.extra.envelope_status.id
      if (!this.envelope[envelopeId]) {
        return {}
      } else {
        return this.envelope[envelopeId]
      }
    }
  },
  created () {
  },
  methods: {
    checkEnvelopStatus (item) {
      let status
      const envelopeStatus = item.extra.envelope_status
      const data = {
        avatar: item.extra.sender.avatar,
        sendername: item.extra.sender.nickname,
        content: item.content,
        users: envelopeStatus.users,
        total: envelopeStatus.total
      }
      if (envelopeStatus.expired) {
        status = 1
      } else {
        const me = envelopeStatus.users.find(user => this.user.id === user.receiver_id)
        if (me) {
          status = 2
          data.amount = me.amount
        } else if (envelopeStatus.total === envelopeStatus.users.length) {
          status = 3
        } else {
          status = 4
        }
      }
      data.status = status
      return {
        data: data,
        id: envelopeStatus.id
      }
    }
  }
}



