
import Icon from 'vue-awesome/components/Icon'
import { mapState } from 'vuex'
import 'vue-awesome/icons/question-circle-o'
import { checkin } from '../api'
import { msgFormatter } from '../utils'
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
name: 'CheckinDialog',
props: {
show: {
type: Boolean
}
},
components: {
Icon,
Spinner
},
data () {
return {
error: '',
loading: false,
checkinSuccess: false,
isSpecial: false,
amount: '',
isShowRule: false
}
},
computed: {
...mapState(['user', 'today', 'systemConfig']),
isCheckin () {
const lastCheckin = this.user.last_checkin
return lastCheckin && !this.$moment(this.today).isAfter(lastCheckin, 'day')
},
checkinSettings () {
return this.systemConfig.checkinSettings || {}
},
days () {
const days = []
const checkinSuccess = this.checkinSuccess
for (let i = 1; i <= 21; i++) {
let day = {num: i, status: '', type: 'normal'}
const continuousCheckin = this.user.continuous_checkins
if (i % 7 === 0) {
day.type = 'special'
}

if (i === continuousCheckin) {
if (checkinSuccess) {
day.status = 'success'
} else if (this.isCheckin) {
day.status = 'taken-today'
} else {
day.status = 'taken'
}
} else if (i === continuousCheckin + 1 && !this.isCheckin) {
day.status = 'takable'
day.amount = this.checkinSettings.single_day_amount
} else if (i < continuousCheckin) {
day.status = 'taken'
}
days.push(day)
}
return days
}
},
watch: {
'show': function (isShow) {
if (isShow) {
this.isSpecial = false
this.checkinSuccess = false
} else {
this.isShowRule = false
}
}
},
methods: {
calcProgress (day) {
const continuousCheckin = this.user.continuous_checkins
if (continuousCheckin >= day) {
return '100%'
}
if (day - continuousCheckin >= 7) {
return '0px'
}
return continuousCheckin % 7 * 10 + 'px'
},
checkin (day) {
if (day.status !== 'takable' || this.loading) {
return
}

this.loading = true
let timeUp = false
setTimeout(() => {
timeUp = true
if (this.checkinSuccess) {
this.loading = false
}
}, 2000)
checkin().then(res => {
if (res.special_reason) {
this.amount = res.special_bonus
this.isSpecial = true
}
return this.$store.dispatch('fetchUser')
}).then(() => {
this.checkinSuccess = true
if (timeUp) {
this.loading = false
}
}).catch(error => {
this.error = msgFormatter(error)
this.loading = false
})
}
}
}



