
import { mapState } from 'vuex'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import { takeEnvelope } from '../api'
import Envelope from './Envelope'
import ImgAsync from './ImgAsync'
import { VISITOR } from '../customConfig'
export default {
components: {
Popup,
Envelope,
XDialog,
XTable,
ImgAsync
},
directives: {
TransferDom
},
props: {
messages: {
default: ''
},
roomId: {
default: ''
}
},
name: 'ChatBody',
data () {
return {
showImageMsg: false,
showImageMsgUrl: '',
roleCache: {},
showEnvelopeDialog: false,
selectedEnvelope: {},
messageCount: 0,
status: '',
busy: false,
imgLoadCount: 0,
notNeedScroll: true,
userLoading: false,
sendRedbag: '',
amount: ''
}
},
computed: {
...mapState([
'user', 'personal_setting', 'rooms', 'envelope'
]),
noPermission () {
return (this.roomId === this.user.default_room_id && (this.personal_setting.banned || this.personal_setting.blocked))
},
statistic () {
if (this.selectedEnvelope) {
const gottenNum = this.selectedEnvelope.users && this.selectedEnvelope.users.length
const total = this.selectedEnvelope.divided_into
if (this.selectedEnvelope.status === 3 || total === gottenNum) {
return `${total}/${total} 已领完`
} else {
return `${gottenNum}/${total} 已领取`
}
} else {
return ''
}
},
success () {
return this.status === 'success'
},
fail () {
return this.status === 'fail'
},
repeat () {
return this.status === 'repeat'
},
taking () {
return this.status === 'taking'
},
sending () {
return this.status === 'sending'
},
expired () {
return this.status === 'expired'
}
},
mounted () {
this.notNeedScroll = false
const view = this.$refs.view
view.scrollTop = view.scrollHeight
},
watch: {
'messages.length': function (newCount, oldCount) {
if (newCount === 0) {
return
}
this.notNeedScroll = false
const view = this.$refs.view
if (oldCount === 0) { // 初始
this.$nextTick(() => {
view.scrollTop = view.scrollHeight
})
} else if ( // 1. user正在閱讀之前訊息 2. 是否為自己發的訊息
view.scrollTop + view.clientHeight + 100 > view.scrollHeight ||
this.messages[newCount - 1].extra.sender.id === this.user.id || this.messages[newCount - 1].extra.type === 5) {
this.$nextTick(() => {
view.scrollTop = view.scrollHeight
})
} else {
this.notNeedScroll = true
}
},
'imgLoadCount': function (count) {
if (count === 0) {
if (!this.notNeedScroll) {
this.$nextTick(() => {
const view = this.$refs.view
view.scrollTop = view.scrollHeight
})
}
}
}
},
methods: {
getRole (id, roles) {
if (this.roleCache[id]) {
return this.roleCache[id]
}
let isManager = ''
let isService = ''
let displayName = ''
for (let i = 0, length = roles.length; i < length; i++) {
displayName = roles[i].display_name
if (roles[i].id === 1) {
isManager = true
break
} else if (roles[i].id === 4) {
isService = true
}
}
if (isManager) {
this.roleCache[id] = {
role: 'manager',
displayName: displayName
}
} else if (isService) {
this.roleCache[id] = {
role: 'service',
displayName: displayName
}
} else {
this.roleCache[id] = {
role: '',
displayName: ''
}
}
return this.roleCache[id]
},
openEnvelop (id, index) {
if (this.user.viewRole === VISITOR) {
this.$vux.toast.show({
text: '游客不能抢红包',
type: 'warn'
})
setTimeout(() => {
this.$router.push('/login')
}, 3000)
return
}
this.showEnvelopeDialog = true
this.busy = true
takeEnvelope(id, this.user.id).then(res => {
this.selectedEnvelope = res
this.busy = false
const status = res.status
this.sendRedbag = this.selectedEnvelope.sender.username
this.status = status

let index = this.messages.findIndex((msg) => msg.extra.type === 5 && msg.extra.envelope_status && msg.extra.envelope_status.id === id)
this.$store.commit('SWITCH_ENVELOPE_STATUS', {
index: index,
status: {
users: res.users
}
})
if (res.amount) {
this.gotMoney = res.amount
this.$store.dispatch('fetchUser').then(() => { this.envelope.status = 'success' })
res.type = 6
this.$parent.$refs.chatFooter.send(res, 6)
} else {
if (res.status === 'expired') {
this.$store.commit('SWITCH_ENVELOPE_STATUS', {
index: index,
status: {
expired: true
}
})
}
}
}).catch(() => { this.busy = false })
},
videoType (str) {
return /\.(flv|avi|wmv|mp4|mov)$/i.test(str)
}
}
}



