
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/volume-up'
import { mapState } from 'vuex'
import { sendEnvelope } from '../api'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Popover from 'vux/src/components/popover/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import { msgFormatter } from '../utils'
import { VISITOR } from '../customConfig'
import ChatBody from './ChatBody'
import ChatFooter from './ChatFooter'
import CheckinDialog from './CheckinDialog'
import Marquee from './Marquee'
const validateItems = ['pack_amount', 'pack_nums']
export default {
components: {
Tab,
TabItem,
AlertModule,
Icon,
Marquee,
Popover,
ChatBody,
ChatFooter,
XDialog,
Group,
XInput,
XTextarea,
XButton,
Swiper,
SwiperItem,
CheckinDialog
},
directives: {
TransferDom
},
data () {
return {
ws: null,
roomMessages: [],
announcementIndex: 0,
msgCnt: '',
showNickNameBox: false,
nickname: this.$store.state.user.nickname,
showImageMsg: false,
showImageMsgUrl: '',
showCheckUser: false,
checkUser: {},
marqueeInterval: '',
assignRoomId: -1,
chatWithId: parseInt(this.$route.params.chatWithId),
isShowEnvelopeDialog: false,
activeSeries: 'symbol',
envelope: {
pack_amount: '',
pack_nums: '',
content: ''
},
loading: false,
error: '',
validators: {
'pack_amount': {
error: '',
validate: (value) => {
if (!value) {
return '请输入金额'
} else {
value = parseInt(value)
if (value < this.systemConfig.envelopeSettings.min_amount) {
return '须高于最低金额限制'
} else if (value > this.systemConfig.envelopeSettings.max_amount) {
return '不能超过最高金额限制'
} else {
return ''
}
}
}
},
'pack_nums': {
error: '',
validate: (value) => {
if (!value) {
return '请输入个数'
} else {
value = parseInt(value)
if (value > this.systemConfig.envelopeSettings.per_max_count) {
return '红包数量超出限制'
} else {
return ''
}
}
}
}
},
VISITOR: VISITOR,
isShowCheckinDialog: false
}
},
computed: {
...mapState([
'user', 'today', 'systemConfig', 'personal_setting', 'announcement', 'rooms', 'messages', 'connectStatus'
]),
noPermission () {
return this.roomId === this.user.default_room_id && (this.personal_setting.banned || this.personal_setting.blocked)
},
isCheckin () {
const lastCheckin = this.user.last_checkin
return lastCheckin && !this.$moment(this.today).isAfter(lastCheckin, 'day')
},
roomId () {
return String(this.user.default_room_id || 1)
}
},
created () {
this.marqueeInterval = setInterval(() => {
this.announcementIndex = (this.announcementIndex + 1) % this.announcement.length
}, 10000)
},
methods: {
inputnum (val, input) {
let formatted = !val ? '' : val.replace(/^[0]|[^0-9]/g, '')
this.$nextTick(() => {
this.envelope[input] = formatted
this.validate(formatted, input)
})
},
showCheckin () {
if (this.user.viewRole === VISITOR) {
this.$router.push('/login')
return
}
if (this.personal_setting.blocked) {
return
}
this.isShowCheckinDialog = true
},
openEnvelopeDialog () {
if (this.user.viewRole === VISITOR) {
this.$router.push('/login')
return
}
if (this.noPermission) {
return
}
this.isShowEnvelopeDialog = true
this.envelope = {
pack_amount: '',
pack_nums: '',
content: ''
}
},
submit () {
if (this.loading) {
return
}
const errors = this.validateAll()
if (errors.length === 0) {
this.loading = true
const envelope = {
pack_amount: parseInt(this.envelope.pack_amount),
pack_nums: parseInt(this.envelope.pack_nums),
content: this.envelope.content || '恭喜发财，大吉大利',
sender_id: this.user.id,
room_id: this.roomId
}
sendEnvelope(envelope).then(data => {
this.hidePanel()
this.loading = false
this.isShowEnvelopeDialog = false
this.$store.dispatch('fetchUser')

envelope['id'] = data.id
envelope['divided_into'] = data.divided_into

this.$refs.chatFooter.send(envelope, 5)
}, error => {
this.error = msgFormatter(error)
this.loading = false
})
} else {
this.error = errors[0]
}
},
reset () {
this.envelope = {}
this.error = ''
},
validate (value, input) {
this.error = this.validators[input].validate(value)
},
validateAll () {
return validateItems
.map(item => this.validators[item].validate(this.envelope[item]))
.filter(msg => msg)
},
hidePanel () {
this.$refs.chatFooter.hidePanel()
}
},
beforeDestroy () {
clearInterval(this.marqueeInterval)
}
}



