
import Icon from 'vue-awesome/components/Icon'
import _ from 'lodash'
import { VISITOR } from '../customConfig'
import { mapState } from 'vuex'
import 'vue-awesome/icons/smile-o'
import 'vue-awesome/icons/paper-plane'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import { sendFileToChat, fetchStickers } from '../api'

export default {
name: 'ChatFooter',
components: {
Swiper,
SwiperItem,
Icon
},
props: {
roomId: {
type: String
},
openEnvelopeDialog: {
type: Function
}
},
data () {
return {
isFocus: false,
isShowControlPanel: false,
isShowEmojiPanel: false,
msgCnt: '',
roomMessages: [],
activeSeries: 'symbol'
}
},
created () {
document.addEventListener('visibilitychange', this.visibilitychange)
},
computed: {
...mapState([
'user', 'systemConfig', 'emojis', 'personal_setting', 'connectStatus'
]),
noPermission () {
return this.roomId === this.user.default_room_id && (this.personal_setting.banned || this.personal_setting.blocked)
},
emojiSeries () {
let symbol = {
name: 'symbol',
display_name: this.emojis.symbol[0] ? this.emojis.symbol[0].emoji : '',
logo: null
}
return [symbol].concat(this.systemConfig.stickerGroups)
},
currentEmojisChunk () {
const emojis = this.emojis[this.activeSeries]
if (this.activeSeries === 'symbol') {
return _.chunk(emojis, 24)
}
return _.chunk(emojis, 8)
}
},
methods: {
visibilitychange () {
this.isFocus = false
if (this.$refs.chatpannel) {
this.$refs.chatpannel.blur()
}
},
triggerRealInput () {
this.$refs.realChatpannel.focus()
},
handTriggerPanel (e) {
let target = e.target
let id = target.id
while (id !== 'typing') {
if (id === 'more-btn') {
this.isShowControlPanel = !this.isShowControlPanel
this.isShowEmojiPanel = false
break
} else if (id === 'emoji-btn') {
if (this.user.viewRole === VISITOR) {
this.$router.push('/login')
return
}
if (this.noPermission) {
return
}
this.isShowControlPanel = false
this.isShowEmojiPanel = !this.isShowEmojiPanel
break
}
target = target.parentNode
id = target.id
if (id === 'typing' || target.nodeName === 'BODY') {
this.hidePanel()
break
}
}
},
hidePanel () {
this.isShowControlPanel = false
this.isShowEmojiPanel = false
},
sendFileMsg (fileInp) {
if (this.user.viewRole === VISITOR) {
this.$router.push('/login')
return
}
let file = fileInp.files[0]
let reg = fileInp.id === 'capture' ? /\.(gif|jpg|jpeg|png)$/i : /\.(flv|avi|wmv|mp4|mov)$/i

if (!reg.test(fileInp.value) || this.noPermission) {
this.$vux.toast.show({
text: '文件格式不正确或您目前尚不符合发言条件',
type: 'warn'
})
return false
}

let formData = new FormData()
formData.append('receiver', this.roomId)
if (fileInp.id === 'capture') {
let size = this.systemConfig.max_media_size.image || 1
if (file.size > size * 1024 * 1024) {
this.$vux.toast.show({
text: '图片尺寸太大，请选择较小尺寸的图片',
type: 'warn'
})
return
}
formData.append('media', file)
sendFileToChat(formData).then((data) => {
this.hidePanel()
fileInp.value = ''
this.send(data, 1)
})
} else {
let size = this.systemConfig.max_media_size.video || 2
if (file.size / 1024 > size * 1024) {
this.$vux.toast.show({
text: '视频尺寸太大，请选择较小尺寸的视频',
type: 'warn'
})
return
}
formData.append('media', file)
sendFileToChat(formData).then((data) => {
this.hidePanel()
fileInp.value = ''
this.send(data, 1)
})
}
},
sendMsg () {
if (this.user.viewRole === VISITOR) {
this.$router.push('/login')
return
}
if (this.connectStatus.status !== 0) {
return this.beforeJoinChatRoom()
}
if (!this.msgCnt.trim()) { return false }
this.send(this.msgCnt, 0)
},
send (message, type) {
let msgData = {
'sender': {
'avatar': this.user.avatar,
'id': this.user.id,
'label': this.user.label || {},
'nickname': this.user.nickname,
'roles': this.user.roles,
'username': this.user.username
},
'type': message && type ? type : 0,
'receivers': this.roomId,
'created_at': new Date().getTime()
}
let sendContent = message && message.url ? message.url : escape(this.msgCnt)
if (message) {
switch (type) {
case 1:
msgData.thumbnail = message.thumbnail
break
case 5:
sendContent = message.content ? message.content : '恭喜发财，大吉大利'
msgData.envelope_status = {
expired: false,
id: message.id,
total: message.divided_into,
users: []
}
break
case 6:
msgData.get_envelope_user = {
id: this.user.id,
nickname: this.user.nickname || this.user.username
}
break
}
}

let content = {
content: sendContent,
extra: JSON.stringify(msgData)
}
let conversationtype = RongIMLib.ConversationType.CHATROOM
let msg = new window.RongIMLib.TextMessage(content)
this.$store.state.ws.sendMessage(conversationtype, this.roomId, msg, {
onSuccess: (message) => {
!this.roomMessages && (this.roomMessages = [])
this.msgCnt = ''
this.$store.dispatch('addMessage', {
content: type === 0 ? unescape(sendContent) : sendContent,
extra: msgData
})
},
onError: (errorCode, message) => {
this.$vux.toast.show({
text: '发送消息失败',
type: 'error'
})
}
})
},
selectSeries (series) {
let seriesName = series.name
this.activeSeries = seriesName
if (seriesName !== 'symbol') {
if (!this.emojis[seriesName]) {
fetchStickers(seriesName).then(resp => {
this.$store.dispatch('initEmoji', {id: seriesName, emojis: resp[seriesName]})
}).catch(() => {})
}
}
},
sendEmojiSymbol (e) {
let target = e.target
if (target.nodeName === 'LI') {
this.msgCnt = this.msgCnt + target.dataset.content
}
},
sendEmojiSticker (e, id) {
let target = e.target
while (target.nodeName !== 'UL') {
if (target.nodeName === 'LI') {
this.send({
url: target.dataset.content
}, 7)
this.hidePanel()
break
}
target = target.parentNode
}
},
checkUploadFile (e) {
if (this.user.viewRole === VISITOR) {
e.preventDefault()
this.$router.push('/login')
}
if (this.noPermission) {
e.preventDefault()
}
}
},
beforeDestroy () {
document.removeEventListener('visibilitychange', this.visibilitychange)
}
}



